body {
    font: 14px Segoe, sans-serif;
    margin: 0 auto;
	background: #212121;
	color: white;
}

.container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 100vh;
}

.input, .output {
	width: 50%;
	padding: 2em;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.input-text {
	min-height: 400px;
	border: 2px solid #464646;
	background: transparent;
	color: white;
	-webkit-transition: border 0.3s;
	-o-transition: border 0.3s;
	transition: border 0.3s;
}

.input-text, .output-text {
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	min-height: 400px;
	border-radius: 4px;
	padding: 1em;
}

.output-text {
	background: #464646;
	overflow-wrap: break-word;
	word-wrap: break-word;
	-webkit-hyphens: auto;
	    -ms-hyphens: auto;
	        hyphens: auto;
}

.input-text:focus {
	border: 2px solid grey;
}

h3 {
	font-weight: normal;
}







